import { animate } from 'animejs';

interface DNDProps {
  happinessUpdateCallback: (happinessModifier: number) => void;
  onReturnToHome: () => void;
}

enum STAT {
  STR = 0,
  DEX = 1,
  CON = 2,
  INT = 3,
  WIS = 4,
  CHA = 5,
}
interface Option {
  text: string;
  success: string;
  failure: string;
  stat: STAT;
}
interface StrReplace {
  strToReplace: string;
  strToReplaceWith: string[];
}
interface Scenario {
  prompt: string;
  options: Option[];
  strReplaces: StrReplace[];
  img: string;
  imgTop: number;
  imgRight: number;
}

const THRESHOLD_TO_PASS = 21;

// Utility function to calculate font size based on text length and container dimensions
const calculateFontSize = (text: string, containerWidth: number, containerHeight: number, maxFontSize: number = 30, minFontSize: number = 12): number => {
  // Create a temporary canvas to measure text
  const canvas = document.createElement('canvas');
  const context = canvas.getContext('2d');

  if (!context) return maxFontSize;

  // Set the font family to match the component
  context.font = `${maxFontSize}px KGPerfectPenmanship`;

  // Measure the text
  const metrics = context.measureText(text);
  const textWidth = metrics.width;

  // Better height calculation - use actual font metrics
  const textHeight = metrics.actualBoundingBoxAscent + metrics.actualBoundingBoxDescent;

  // If text fits, return max font size
  if (textWidth <= containerWidth && textHeight <= containerHeight) {
    return maxFontSize;
  }

  // Calculate the scale factor needed to fit the text
  const scaleX = containerWidth / textWidth;
  const scaleY = containerHeight / textHeight;

  // Be more aggressive about using height - prioritize height scaling for better readability
  const scale = Math.min(scaleX, scaleY * 0.8); // Use 80% of height to ensure some padding

  // Calculate new font size
  let newFontSize = Math.floor(maxFontSize * scale);

  // Ensure font size is within bounds
  newFontSize = Math.max(minFontSize, Math.min(maxFontSize, newFontSize));

  return newFontSize;
};

