const SCENARIOS = [SCENARIO_1, SCENARIO_2];

const DND: React.FC<DNDProps> = ({ happinessUpdateCallback, onReturnToHome }) => {
  const [currMainText, setCurrMainText] = useState<string>("");
  const [stats, setStats] = useState<number[]>([]);
  const [diceValue, setDiceValue] = useState<number | null>(null);
  const [isRolling, setIsRolling] = useState<boolean>(false);
  const [isGameFinished, setIsGameFinished] = useState<boolean>(false);
  const [showDice, setShowDice] = useState<boolean>(false);
  const [updatedScenario, setUpdatedScenario] = useState<Scenario>({
    prompt: "",
    options: [],
    strReplaces: [],
    img: "",
    imgTop: 0,
    imgRight: 0,
  });
  const buttonWidth = GAME_WIDTH / 2.2;
  const buttonHeight = 120;
  const outlineThickness = 2;

  const processScenario = (scenario: Scenario): Scenario => {
    // Create a deep copy of the scenario to avoid mutating the original
    const updatedScenario: Scenario = {
      prompt: scenario.prompt,
      options: scenario.options.map(option => ({
        text: option.text,
        success: option.success,
        failure: option.failure,
        stat: option.stat
      })),
      strReplaces: scenario.strReplaces,
      img: scenario.img,
      imgTop: scenario.imgTop,
      imgRight: scenario.imgRight,
    };

    // Iterate through each strReplace item
    scenario.strReplaces.forEach(strReplace => {
      // Randomly pick ONE value from strToReplaceWith
      const randomReplacement = strReplace.strToReplaceWith[
        Math.floor(Math.random() * strReplace.strToReplaceWith.length)
      ];

      // Create regex pattern to replace all instances (global flag)
      const regex = new RegExp(strReplace.strToReplace, 'g');

      // Replace in prompt
      updatedScenario.prompt = updatedScenario.prompt.replace(regex, randomReplacement);

      // Replace in all options (text, success, and failure)
      updatedScenario.options.forEach(option => {
        option.text = option.text.replace(regex, randomReplacement);
        option.success = option.success.replace(regex, randomReplacement);
        option.failure = option.failure.replace(regex, randomReplacement);
      });
    });

    // Randomly select 2 options from the scenario
    const shuffledOptions = [...updatedScenario.options];
    for (let i = shuffledOptions.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [shuffledOptions[i], shuffledOptions[j]] = [shuffledOptions[j], shuffledOptions[i]];
    }

    // Keep only the first 2 options
    updatedScenario.options = shuffledOptions.slice(0, 2);

    return updatedScenario;
  }

  // Function to generate an array of 6 numbers: 15, 14, 13, 12, 10, 8
  // Randomly shuffles their positions
  const generateRandomStats = (): number[] => {
    const stats: number[] = [15, 14, 13, 12, 10, 8];

    // Shuffle the array to randomize positions
    for (let i = stats.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [stats[i], stats[j]] = [stats[j], stats[i]];
    }

    return stats;
  };

  const checkIfPassed = (statVal: number, roll: number) => {
    return statVal + roll >= THRESHOLD_TO_PASS;
  }

  const runResultLogic = (isPassed: boolean, scenarioOption: Option) => {
    setCurrMainText(isPassed ? scenarioOption.success : scenarioOption.failure);

    // Hide dice
    animate(".dice-container", {
      opacity: [1, 0],
      duration: 1000,
      easing: 'easeInOutSine',
      delay: 1500,
    })

    // Make modifications to happiness
    happinessUpdateCallback(isPassed ? 15 : -8);
    setIsGameFinished(true);
  }

  // Function to roll a d20 with animation
  const rollD20 = (scenarioOption: Option) => {
    animate(".dnd-bottom-btn", {
      opacity: [1, 0],
      translateY: [0, 100],
      duration: 125,
      easing: 'easeInOutSine',
    })

    if (isRolling) return; // Prevent multiple rolls while animating

    setIsRolling(true);
    setShowDice(true);
    setDiceValue(null);

    // Generate random values during animation
    const animationDuration = 2000; // 2 seconds
    const interval = 50; // Update every 50ms
    const iterations = animationDuration / interval;
    let currentIteration = 0;

    const animationInterval = setInterval(() => {
      const randomValue = Math.floor(Math.random() * 20) + 1;
      setDiceValue(randomValue);
      currentIteration++;

      if (currentIteration >= iterations) {
        clearInterval(animationInterval);
        const finalValue = Math.floor(Math.random() * 20) + 1;
        setDiceValue(finalValue);
        setIsRolling(false);

        const isPassed = checkIfPassed(stats[scenarioOption.stat], finalValue);
        runResultLogic(isPassed, scenarioOption);
      }
    }, interval);
  };

  useEffect(() => {
    setStats(generateRandomStats());
    const randomScenario = SCENARIOS[Math.floor(Math.random() * SCENARIOS.length)];
    const updatedScenario = processScenario(randomScenario);
    setUpdatedScenario(updatedScenario);
    setCurrMainText(updatedScenario.prompt);
  }, []);

  const generateStats = (stats: number[]) => {
    return (
      <div style={{
        display: 'flex',
        flexDirection: 'column',
      }}>
        {stats.map((stat, index) => (
          <div key={index} style={{
            fontSize: '40px',
            fontFamily: 'KGPerfectPenmanship',
            color: 'white',
            zIndex: 1000,
            textShadow: `-${outlineThickness}px -${outlineThickness}px 0 #000, ${outlineThickness}px -${outlineThickness}px 0 #000, -${outlineThickness}px ${outlineThickness}px 0 #000, ${outlineThickness}px ${outlineThickness}px 0 #000`,
            textAlign: 'center',
            marginBottom: 0.5,
          }}>
            {stat}
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className="dnd-container" style={{
      position: 'absolute',
      top: 0,
      left: 0,
      zIndex: 1000,
      width: GAME_WIDTH,
      height: GAME_HEIGHT,
      margin: MARGINS,
      backgroundImage: `url(${dndBackground})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat',
    }}>
      <img src={statSheet} alt="stat-sheet" style={{
        position: 'absolute',
        top: 46,
        left: 0,
        zIndex: 1000,
      }} />
      <div style={{
        position: 'absolute',
        top: 74,
        left: 26,
      }}>
        {generateStats(stats)}
      </div>

      <img src={bozo} alt="dnd-scenario" style={{
        position: 'absolute',
        top: 238,
        left: 135,
        transform: 'scaleX(-1)',
      }} />
      <img src={updatedScenario.img} alt="dnd-scenario" style={{
        position: 'absolute',
        top: updatedScenario.imgTop,
        right: updatedScenario.imgRight,
      }} />
      <div className="dnd-content" style={{
        position: 'absolute',
        top: 580,
        left: 168,
        width: 1080,
        height: 280,
        backgroundImage: `url(${textBoxSmall})`,
        backgroundSize: 'contain',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center'
      }}>
        <div style={{
          width: 900,
          height: 180,
          // fontSize: `${calculateFontSize(currMainText, 900, 180, 40, 16)}px`,
          fontSize: `25px`,
          fontFamily: 'KGPerfectPenmanship',
          position: 'absolute',
          top: 70,
          left: 90,
          zIndex: 1000,
        }}>
          {currMainText}
        </div>
      </div>

      {/* Dice Display */}
      {showDice && (
        <div className="dice-container" style={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          zIndex: 1003,
        }}>
          <div className="dice" style={{
            width: 120,
            height: 120,
            backgroundImage: `url(${dndDice})`,
            backgroundSize: 'contain',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            fontSize: '48px',
            fontFamily: 'KGPerfectPenmanship',
            fontWeight: 'bold',
            color: 'white',
            animation: isRolling ? 'diceRoll 0.5s infinite' : 'diceStop 0.5s ease-out',
          }}>
            {diceValue || '?'}
          </div>
        </div>
      )}

      {/* Bottom buttons container */}
      <div style={{
        position: 'absolute',
        bottom: 80,
        left: '50%',
        transform: 'translateX(-50%)',
        display: 'flex',
        gap: '20px',
        zIndex: 1001,
      }}>
        <button className="dnd-bottom-btn" disabled={isRolling} onClick={() => rollD20(updatedScenario?.options[0])} style={{
          width: buttonWidth,
          height: buttonHeight,
          backgroundColor: '#FF8C00',
        }}>
          {updatedScenario?.options[0]?.text}
        </button>

        <button className="dnd-bottom-btn" disabled={isRolling} onClick={() => rollD20(updatedScenario?.options[1])} style={{
          width: buttonWidth,
          height: buttonHeight,
          backgroundColor: '#FF8C00',
        }}>
          {updatedScenario?.options[1]?.text}
        </button>

        {
          isGameFinished &&
          <button
            className="return-home-btn"
            onClick={() => {
              onReturnToHome();
            }}
          >
            Return Home
          </button>
        }
      </div>
    </div>
  );
};

export default DND;
