import React, { useEffect, useState } from 'react';
import { formatTime } from './Race';
import { RACER_NAME } from '../Game';

import raceResultsBg from '../../../assets/imgs/race-results.png';
import raceContinueButton from '../../../assets/imgs/race-continue-button.png';

import './Results.css';

interface Racer {
  name: string;
  image: string;
  speed: number;
  x: number;
  isFinished: boolean;
  finishTime: number | null;
}

interface ResultsProps {
  racers: Racer[];
  raceNumber: number;
  onContinue: (outcome: 'continue' | 'victory' | 'failure') => void;
  wasPlayerFirstPlace: (wasPlayerFirstPlace: boolean, raceNumber: number) => void;
}

const RACE_THRESHOLDS = [4, 3, 1];

const Results: React.FC<ResultsProps> = ({ racers, raceNumber, onContinue, wasPlayerFirstPlace }) => {
  // Sort racers by finish time (fastest first)
  const sortedRacers = [...racers]
    .sort((a, b) => (a.finishTime || 0) - (b.finishTime || 0));

    console.log(sortedRacers);

  const determineNextScreen = (placement: number) => {
    const currentThreshold = RACE_THRESHOLDS[raceNumber];

    // Check if player meets the threshold for this race
    // Since placement is 0-based, we need to add 1 to compare with threshold
    if (placement + 1 > currentThreshold) {
      // Player failed to meet threshold - show gameover
      console.log(`damn u suck`);
      return 'failure';
    }

    // Check if this is the last race
    if (raceNumber === RACE_THRESHOLDS.length - 1) {
      // Last race and player met threshold - show victory
      console.log(`BEEG WIN`);
      return 'victory';
    } else {
      // Not the last race, player met threshold - continue to next race
      console.log(`Race ${raceNumber + 1} completed! Player placed ${placement + 1}/${currentThreshold} required.`);
      return 'continue';
    }
  }

  const handleContinueClick = () => {
    const playerIndex = sortedRacers.findIndex(racer => racer.name === RACER_NAME);
    if (playerIndex !== -1) {
      const outcome = determineNextScreen(playerIndex);
      onContinue(outcome);
    }
  };

  useEffect(() => {
    if (sortedRacers[0].name === RACER_NAME) {
      wasPlayerFirstPlace(true, raceNumber);
    }
  }, []);

  return (
    <div className="results-container">
      {/* Background Image */}
      <img
        src={raceResultsBg}
        alt="Race Results Background"
        className="results-background"
      />
      {sortedRacers.map((racer, index) => (
        <div key={racer.name} className="result-item" style={{ zIndex: 100, position: 'absolute', top: 260 + index * 145, left: 250 }}>
          <img
            src={racer.image}
            alt={racer.name}
            className="racer-image"
          />
          <span className="racer-name">{racer.name}</span>
          <div className="finish-time">
            {formatTime(racer.finishTime || 0)}
          </div>
        </div>
      ))}
      <div style={{ position: 'absolute', top: 180, right: 320 }}>
        <img src={raceContinueButton} alt="Continue" className="continue-button" onClick={handleContinueClick} />
      </div>
    </div>
  );
};

export default Results;
