import React, { useEffect, useRef } from 'react';
import StreamPcImg from '../../../assets/imgs/stream-pc.png';
import { animate, createTimeline } from 'animejs';
import './StreamPc.css';

interface StreamPcProps {
  // Add your props here
}

const StreamPc: React.FC<StreamPcProps> = (props) => {
  const streamPcRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Force reset the DOM element to its initial state
    if (streamPcRef.current) {
      streamPcRef.current.style.transform = 'translateX(1500px)';
    }

    // Stop any existing animations on this element and reset transforms
    animate(".stream-pc", {
      translateX: 1500,
      duration: 0,
    });

    // Small delay to ensure reset is complete
    setTimeout(() => {
      // Create and store the main timeline
      const streamPcTimeline = createTimeline()
        .add(".stream-pc", {
          translateX: [1500, 0],
          duration: 1500,
        })
    }, 50);

    // Clean up function
    return () => {
      // Force reset the DOM element
      if (streamPcRef.current) {
        streamPcRef.current.style.transform = 'translateX(1500px)';
      }

      // Stop any ongoing animations when component unmounts
      animate(".stream-pc", {
        translateX: 1500,
        duration: 0,
      });
    };
  }, []);

  return (
    <div ref={streamPcRef} className="stream-pc" style={{ position: 'absolute', bottom: -20, left: 250, zIndex: 1 }}>
      <img src={StreamPcImg} alt="Stream PC" />
    </div>
  );
};

export default StreamPc;
