const SPEED_MODE_ENABLED = false;

const TRACK_LENGTH = 1200;
const RACE_DURATION = SPEED_MODE_ENABLED ? 1 : 60000;

// Function to format time as MM:SS.HH (input in milliseconds)
export const formatTime = (timeInMilliseconds: number): string => {
  const totalSeconds = Math.floor(timeInMilliseconds / 1000);
  const minutes = Math.floor(totalSeconds / 60);
  const seconds = totalSeconds % 60;
  const hundredths = Math.floor((timeInMilliseconds % 1000) / 10);

  return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}.${hundredths.toString().padStart(2, '0')}`;
};

// Function to generate speed-based time penalty (0-10 seconds)
// Higher speed = better chance of lower penalty
const generateSpeedPenalty = (speed: number): number => {
  // Configuration constants
  const MAX_PENALTY = SPEED_MODE_ENABLED ? 3 : 30; // Maximum penalty in seconds
  const MIN_PENALTY = 0;  // Minimum penalty in seconds
  const STD_DEV = 10;     // Standard deviation for the bell curve (controls spread)

  // Generate a random value from a normal distribution centered at the speed
  let penalty: number;
  do {
    // Box-Muller transform to generate normal distribution
    const u1 = Math.random();
    const u2 = Math.random();
    const z0 = Math.sqrt(-2 * Math.log(u1)) * Math.cos(2 * Math.PI * u2);

    // Center the distribution at the speed value, convert to penalty range
    // Higher speed = lower penalty, so we invert the relationship
    const meanPenalty = MAX_PENALTY * (1 - speed / 100);
    penalty = meanPenalty + z0 * (STD_DEV / 100) * MAX_PENALTY;
  } while (penalty < MIN_PENALTY || penalty > MAX_PENALTY);

  return Math.max(MIN_PENALTY, Math.min(MAX_PENALTY, penalty));
};

const RACE_1_NAME = 'Milky Meadows - Top 4 Advance';
const RACE_2_NAME = 'Peaky Peaks - Top 3 Advance';
