const RACE_3_RACERS: Racer[] = [
  { name: 'Glorp Ship', image: bozoRace3Racer3, speed: 45, x: 0, isFinished: false, finishTime: null, lane: 0 },
  { name: 'Prison Break', image: bozoRace3Racer2, speed: 55, x: 0, isFinished: false, finishTime: null, lane: 1 },
  { name: 'Fire Flier', image: bozoRace3Racer4, speed: 40, x: 0, isFinished: false, finishTime: null, lane: 2 },
  { name: 'FUCK YOU', image: bozoRace3Racer1, speed: 95, x: 0, isFinished: false, finishTime: null, lane: 3 },
];

const RACERS = [RACE_1_RACERS, RACE_2_RACERS, RACE_3_RACERS];

const NUM_RACERS = 5;

const Race: React.FC<RaceProps> = ({ raceNumber, currRacer, onRaceOutcome, wasPlayerFirstPlace, fadeComplete }) => {
  const [currSpeechBubble, setCurrSpeechBubble] = useState('');
  const [raceStarted, setRaceStarted] = useState(false);
  const racerTimelinesRef = useRef<any[]>([]);
  const [raceTime, setRaceTime] = useState(0);
  const timerRef = useRef<HTMLDivElement>(null);
  const [renderKey, setRenderKey] = useState(0);
  const [numRacersFinished, setNumRacersFinished] = useState(0);
  const [countdownPhase, setCountdownPhase] = useState<'idle' | 'counting' | 'go' | 'complete'>('idle');
  const [currentCountdownImage, setCurrentCountdownImage] = useState<string | null>(null);
  const [placedDivs, setPlacedDivs] = useState<Array<{ id: number; index: number; top: number }>>([]);

  // Function to place a div on screen with vertical stacking
  const placeDivOnScreen = (index: number) => {
    if (index < 0 || index > 4) {
      console.error('Index must be between 0 and 4');
      return;
    }

    const verticalSpacing = 150; // Set amount for vertical spacing
    const topPosition = 80 + index * verticalSpacing;
    const divId = Date.now(); // Unique ID for the div

    const newDiv = {
      id: divId,
      index: index,
      top: topPosition
    };

    setPlacedDivs(prev => [...prev, newDiv]);
  };

  // Function to remove a div by ID
  const removeDiv = (divId: number) => {
    setPlacedDivs(prev => prev.filter(div => div.id !== divId));
  };

  // Function to clear all divs
  const clearAllDivs = () => {
    setPlacedDivs([]);
  };

  useEffect(() => {
    // Reset the player's finish time if for some reason the race is repeated
    RACERS.forEach(race => {
      race.forEach(racer => {
        racer.finishTime = null;
      });
    });
  }, []);

  // Setup nametag animations only once when component mounts
  useEffect(() => {
    if (!raceStarted) {
      const animTimeout = setTimeout(() => {
        for (let racerNumber = 0; racerNumber < NUM_RACERS; racerNumber++) {
          const racerClassName = `racer-${racerNumber}-${raceNumber}`;
          const staggerDelay = 1000 + 100 * racerNumber;
          const nametagTimeline = createTimeline();
          nametagTimeline
            .add(`.${racerClassName}-nametag`, {
              opacity: [0, 1],
              translateX: [0, 100],
              duration: 1000,
              easing: 'easeInOutSine',
              delay: staggerDelay,
            })
            .add(`.${racerClassName}-nametag`, {
              opacity: [1, 0],
              translateX: [100, 0],
              duration: 1000,
              easing: 'easeInOutSine',
              delay: 5000 + staggerDelay,
              onComplete: (self: any) => {
                self.display = 'none';
              }
            });
        }
      }, 1000);

      return () => clearTimeout(animTimeout);
    }
  }, [raceNumber]); // Only run when raceNumber changes

  // Helper function to generate race image assets
  const generateRaceImages = (raceNumber: number, currRacer: Racer): React.ReactElement[] => {
    const raceImages = {
      0: [raceFirst1, raceFirst2, raceFirst3, raceFirst4, raceFirst5, raceFirst6],
      1: [raceSecond1, raceSecond2, raceSecond3, raceSecond4, raceSecond5, raceSecond6],
      2: [raceThird1, raceThird2, raceThird3, raceThird4, raceThird5, raceThird6]
    };

    const images = raceImages[raceNumber as keyof typeof raceImages];

    if (!images) {
      console.error(`Invalid race number: ${raceNumber}. Must be 1, 2, or 3.`);
      return [];
    }

    return images.map((imageSrc, index) => (
      <div>
        <img
          key={`race-${raceNumber}-${index + 1}`}
          src={imageSrc}
          alt={`Race ${raceNumber} layer ${index + 1}`}
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            zIndex: index * 2,
            width: '100%',
            height: '100%',
            objectFit: 'contain'
          }}
        />
        {
          index < 5 &&
          generateRacer(index, raceNumber, currRacer)
        }
      </div>
    ));
  };

  useEffect(() => {
    if (currRacer && RACERS[raceNumber].length < NUM_RACERS) {
      RACERS[raceNumber].push(currRacer);
    }
  }, [currRacer, raceNumber]);

  const generateRacer = (racerNumber: number, raceNumber: number, currRacer: Racer) => {
    const racer = racerNumber === NUM_RACERS - 1 ? currRacer : RACERS[raceNumber][racerNumber];
    const racerClassName = `racer-${racerNumber}-${raceNumber}`;

    return (
      <div key={`racer-container-${racerNumber}-${raceNumber}`}>
        <img
          className={racerClassName}
          alt="Racer"
          src={racer.image}
          style={{
            position: 'absolute',
            top: 75 + racerNumber * 145,
            left: 0,
            zIndex: racerNumber * 2 + 1,
          }}
        />
        {/* Racer Name Text Box */}
        <div
          className={`${racerClassName}-nametag`}
          style={{
            opacity: 0,
            position: 'absolute',
            top: 80 + racerNumber * 145,
            left: 20,
            zIndex: racerNumber * 2 + 2,
            backgroundColor: 'rgba(0, 0, 0, 0.8)',
            color: 'white',
            padding: '5px 10px',
            borderRadius: '5px',
            fontFamily: 'Kindergarten',
            fontSize: '24px',
            textAlign: 'center',
            minWidth: '120px',
            border: '2px solid white',
            boxShadow: '0 2px 4px rgba(0, 0, 0, 0.3)',
          }}
        >
          {racer.name}
        </div>
      </div>
    );
  };

  const generateRacerTimelines = (raceNumber: number, currRacer: Racer) => {
    for (let i = 0; i < NUM_RACERS; i++) {
      const racerNumber = i;
      const racer = racerNumber === NUM_RACERS - 1 ? currRacer : RACERS[raceNumber][racerNumber];
      const racerClassName = `racer-${racerNumber}-${raceNumber}`;

      // Function to generate equal-length track segments
      const generateTrackSegments = (numSegments: number): [number, number][] => {
        if (numSegments <= 0) return [];

        const segmentLength = TRACK_LENGTH / numSegments;
        const segments: [number, number][] = [];

        for (let i = 0; i < numSegments; i++) {
          const start = i * segmentLength;
          const end = (i + 1) * segmentLength;
          segments.push([start, end]);
        }

        return segments;
      };

      const racerTimeline = createTimeline()
      const numSegments = Math.floor(Math.random() * 10) + 1;
      const segments = generateTrackSegments(numSegments);

      let racerTotalSpeedPenalty = 0;
      segments.forEach((segment, index) => {
        // Generate speed-based penalty for this segment
        const speedPenalty = generateSpeedPenalty(racer.speed) / numSegments;
        const baseDuration = RACE_DURATION / numSegments;
        const finalDuration = baseDuration + speedPenalty * 1000; // Convert seconds to milliseconds

        racerTotalSpeedPenalty += speedPenalty;
        racerTimeline.add(`.${racerClassName}`, {
          translateX: [segment[0], segment[1]],
          duration: finalDuration,
          onUpdate: (self) => {
            if (index === numSegments - 1 && self.progress === 1) {
              console.log(`${racer.name} finished with speed ${racer.speed}, penalty: ${racerTotalSpeedPenalty.toFixed(2)}s`);
              setNumRacersFinished(prev => prev + 1);
              placeDivOnScreen(racer.lane);
            }
          },
        })
      });

      const finishTime = RACE_DURATION + racerTotalSpeedPenalty * 1000;
      racer.finishTime = finishTime;
      RACERS[raceNumber][i] = racer;
      if (i === NUM_RACERS - 1) {
        currRacer.finishTime = finishTime;
        RACERS[raceNumber][4] = currRacer;
      }

      // Store the timeline for later use
      racerTimelinesRef.current.push(racerTimeline);
    }
  }

  useEffect(() => {
    if (currSpeechBubble !== '') {
      animate(".speech-bubble", {
        translateY: [400, -5, 0],
        duration: 300,
        easing: 'inOutBounce',
      });
    } else {
      animate(".speech-bubble", {
        translateY: [0, 400],
        duration: 300,
        easing: 'easeInQuad',
      });
    }
  }, [currSpeechBubble]);

  const updateSpeechBubble = (text: string) => {
    setCurrSpeechBubble(text);
  }

  const cycleDialogue = (dialogueArray: string[]) => {
    let currentIndex = 0;

    const showText = () => {
      if (currentIndex < dialogueArray.length) {
        updateSpeechBubble(dialogueArray[currentIndex]);

        // Schedule hiding the text after 10 seconds
        setTimeout(() => {
          updateSpeechBubble('');

          // Schedule showing next text after 3 seconds (unless it's the last one)
          if (currentIndex < dialogueArray.length - 1) {
            setTimeout(() => {
              currentIndex++;
              showText();
            }, 3000);
          }
        }, 10000);
      }
    };

    // Start the cycle
    showText();
  };

  const pickRandomDialogue = (dialogueArray: string[]) => {
    setCurrSpeechBubble(dialogueArray[Math.floor(Math.random() * dialogueArray.length)]);
  }

  const startCountdown = () => {
    setCountdownPhase('counting');
    
    // Show countdown 3
    setCurrentCountdownImage(countdown3);
    
    setTimeout(() => {
      // Show countdown 2
      setCurrentCountdownImage(countdown2);
      
      setTimeout(() => {
        // Show countdown 1
        setCurrentCountdownImage(countdown1);
        
        setTimeout(() => {
          // Show countdown race (GO!)
          setCountdownPhase('go');
          setCurrentCountdownImage(countdownRace);
          
          setTimeout(() => {
            // Countdown complete, start the race
            setCountdownPhase('complete');
            setCurrentCountdownImage(null);
            console.log('Countdown complete! Race starting...');
            startRace();
          }, 1000); // Show "GO!" for 1 second
          
        }, 1000); // Show "1" for 1 second
        
      }, 1000); // Show "2" for 1 second
      
    }, 1000); // Show "3" for 1 second
  };

  const startRace = () => {
    if (!raceStarted) {
      pickRandomDialogue(RACE_DIALOGUES[raceNumber]);

      generateRacerTimelines(raceNumber, currRacer);

      setRaceStarted(true);

      createTimer({
        onUpdate: self => {
          if (timerRef.current != null) {
            timerRef.current.textContent = formatTime(self.currentTime);
          }
        }
      });

      // Start all racer timelines simultaneously
      racerTimelinesRef.current.forEach((timeline: any) => {
        timeline.play();
      });
    }
  };

  const resetRace = () => {
    setRaceStarted(false);
    setRaceTime(0);
    setNumRacersFinished(0);
    setCountdownPhase('idle');
    setCurrentCountdownImage(null);

    // Clear timelines and reset racers
    racerTimelinesRef.current = [];

    // Reset all racers to starting position
    const racers = document.querySelectorAll('[class^="racer-"]');
    racers.forEach(racer => {
      (racer as HTMLElement).style.transform = 'translateX(0px)';
    });

    // Force a re-render to recreate timelines
    setRenderKey(prev => prev + 1);
  };

  useEffect(() => {
    resetRace();
  }, []);

  return (
    <>
      {numRacersFinished === NUM_RACERS ? <Results racers={RACERS[raceNumber]} raceNumber={raceNumber} onContinue={onRaceOutcome} wasPlayerFirstPlace={wasPlayerFirstPlace} /> :
        <div className="race-container">
          {/* Timer Display */}
          <div ref={timerRef} style={{
            position: 'absolute',
            top: 0,
            right: 40,
            zIndex: 100000,
            fontFamily: 'Kindergarten',
            fontSize: '40px',
            fontWeight: 'bold',
            color: 'black',
            padding: '10px 15px',
            borderRadius: '5px',
            textAlign: 'left',
            minWidth: '120px',
            width: '120px',
          }}>
            {formatTime(raceTime)}
          </div>

          {/* Countdown Overlay */}
          {currentCountdownImage && (
            <div style={{
              position: 'absolute',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
              zIndex: 100001,
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: 'rgba(0, 0, 0, 0.5)',
            }}>
              <img 
                src={currentCountdownImage} 
                alt="Countdown" 
                style={{
                  objectFit: 'contain',
                }}
              />
            </div>
          )}
          {
            fadeComplete &&
            <button
            onClick={startCountdown}
            disabled={raceStarted || countdownPhase !== 'idle'}
            className="race-button"
            style={{
              display: raceStarted ? 'none' : 'block',
              position: 'absolute',
              fontFamily: 'KGPerfectPenmanship',
              top: 900,
              left: 1000,
              zIndex: 100000,
              padding: '10px 20px',
              fontSize: '50px',
              backgroundColor: (raceStarted || countdownPhase !== 'idle') ? '#ccc' : '#4CAF50',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
            }}
          >
            Start Race
          </button>
          }
          <div key={renderKey}>
            {generateRaceImages(raceNumber, currRacer)}
          </div>
          <img src={raceBg} alt="Race Background" style={{ position: 'absolute', top: 0, left: 0, zIndex: 100 }} />

          <div style={{
            position: 'absolute',
            top: 10,
            left: 114,
            zIndex: 100,
            fontFamily: 'Kindergarten',
            fontSize: 40,
            color: 'black',
            fontWeight: 'bold',
            textAlign: 'center',
            lineHeight: '1',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            height: '50px',
          }}>
            {RACE_NAMES[raceNumber]}
          </div>
          <img alt="Reya" src={raceReya} style={{ position: 'absolute', top: 809, left: 41, zIndex: 100 }} />
          <div className="speech-bubble" style={{ position: 'absolute', top: 815, left: 238, zIndex: 100 }} >
            <img alt="Speech Bubble" src={speechBubble} />
            <div style={{ position: "absolute", top: 55, left: 110, zIndex: 100, fontFamily: 'KGPerfectPenmanship', fontSize: 30, color: 'black', width: 930 }}>
              {currSpeechBubble}
            </div>
          </div>

          {/* Rendered placed divs */}
          {placedDivs.map(div => (
            <div
              key={div.id}
              className="finished-div-animation"
              style={{
                position: 'absolute',
                top: div.top,
                right: 200,
                width: '200px',
                height: '80px',
                backgroundColor: 'rgba(255, 255, 255, 0.9)',
                border: '2px solid #333',
                borderRadius: '10px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontFamily: 'KGPerfectPenmanship',
                fontSize: '30px',
                color: '#333',
                zIndex: 1000,
                boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)'
              }}
            >
              Finished!
            </div>
          ))}
        </div>
      }
    </>
  );
};

export default Race;
