import React from 'react';
import './MainMenu.css';
import mainMenuBg from '../assets/imgs/main-menu.png';
import startButtonImg from '../assets/imgs/main-menu-play.png';

interface MainMenuProps {
  onPlayGame: () => void;
}

const MainMenu: React.FC<MainMenuProps> = ({ onPlayGame }) => {
  return (
    <div 
      className="main-menu" 
      style={{ 
        width: "100%", 
        height: "100%",
        backgroundImage: `url(${mainMenuBg})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat'
      }}
    >
      <div className="main-menu-content">
        <div style={{ position: 'absolute', top: 560, left: 445, cursor: "pointer" }}>
          <img src={startButtonImg} onClick={onPlayGame} alt="Main Menu" />
        </div>
      </div>
    </div>
  );
};

export default MainMenu;
