import React, { useState } from 'react';
import MainMenu from './components/MainMenu';
import { GAME_HEIGHT, GAME_WIDTH, MARGINS } from './constants';
import PickBozo from './components/PickBozo';
import Game from './components/Game/Game';
import './App.css';
import DND from './components/Game/DND/DND';

const enum SCENE {
  MAIN_MENU,
  PICK_BOZO,
  GAME,
}

function App() {
  const [currScene, setCurrScene] = useState(SCENE.MAIN_MENU);

  const handlePlayGame = () => {
    setCurrScene(SCENE.GAME);
  }

  const handlePickBozo = () => {
    setCurrScene(SCENE.GAME);
  }

  const handleReturnToMainMenu = () => {
    setCurrScene(SCENE.MAIN_MENU);
  }

  return (
    <div className="App">
      <div style={{
        width: GAME_WIDTH,
        height: GAME_HEIGHT,
        marginTop: MARGINS,
        marginLeft: MARGINS,
      }}>
        {
          currScene === SCENE.MAIN_MENU && <MainMenu onPlayGame={handlePlayGame} />
        }
        {
          currScene === SCENE.PICK_BOZO && <PickBozo handlePickBozo={handlePickBozo} />
        }
        {
          currScene === SCENE.GAME && <Game onReturnToMainMenu={handleReturnToMainMenu} />
        }
      </div>
    </div>
  );
}

export default App;
