import React, { useEffect, useState, useRef } from 'react';
import { animate } from 'animejs';

// Import food-related assets
import foodCheese from '../../../assets/imgs/food-cheese.png';
import foodCheeseEaten1 from '../../../assets/imgs/food-cheese-eaten-1.png';
import foodCheeseEaten2 from '../../../assets/imgs/food-cheese-eaten-2.png';
import foodCheeseEaten3 from '../../../assets/imgs/food-cheese-eaten-3.png';

interface EatFoodProps {
}

const EatFood: React.FC<EatFoodProps> = () => {
  const [isEating, setIsEating] = useState(false);
  const [eatingProgress, setEatingProgress] = useState(0);
  const [isVisible, setIsVisible] = useState(false);
  const [currentFoodImage, setCurrentFoodImage] = useState(foodCheese);
  const eatFoodRef = useRef<HTMLDivElement>(null);
  const hasAnimatedRef = useRef(false);

  const handleEatFood = () => {
    setIsEating(true);
    setEatingProgress(0);
    
    // Simulate eating animation
    const eatingInterval = setInterval(() => {
      setEatingProgress(prev => {
        const newProgress = prev + 10;
        
        // Update food image based on progress
        if (newProgress <= 25) {
          setCurrentFoodImage(foodCheese);
        } else if (newProgress <= 50) {
          setCurrentFoodImage(foodCheeseEaten1);
        } else if (newProgress <= 75) {
          setCurrentFoodImage(foodCheeseEaten2);
        } else {
          setCurrentFoodImage(foodCheeseEaten3);
          setIsVisible(false);
        }
        
        if (newProgress >= 100) {
          clearInterval(eatingInterval);
          setIsEating(false);
          return 0;
        }
        
        return newProgress;
      });
    }, 200);
  };

  useEffect(() => {
    // Only run animation once per component mount
    if (hasAnimatedRef.current) {
      return;
    }
    
    hasAnimatedRef.current = true;

    // Force reset the DOM element to its initial state
    if (eatFoodRef.current) {
      eatFoodRef.current.style.transform = 'none';
    }

    // Reset any existing transforms first
    animate(".eat-food-container", {
      translateY: 0,
      translateX: 0,
      rotate: 0,
      scale: 1,
      duration: 0
    });

    // Small delay to ensure reset is complete
    setTimeout(() => {
      animate(".eat-food-container", {
        translateY: [-1000, 0],
        rotate: [100, 0],
        onBegin: self => {
          setIsVisible(true);
        },
        onComplete: self => {
          handleEatFood();
          // Reset transforms after animation completes to prevent interference
          setTimeout(() => {
            if (eatFoodRef.current) {
              eatFoodRef.current.style.transform = 'none';
            }
            animate(".eat-food-container", {
              translateY: 0,
              translateX: 0,
              rotate: 0,
              scale: 1,
              duration: 0
            });
          }, 100);
        }
      });
    }, 50);
  }, []);

  return (
    <>
      <div ref={eatFoodRef} className="eat-food-container" style={{ position: 'absolute', left: 70, top: 300 }}>
        {isVisible && (
          <img 
            src={currentFoodImage} 
            alt="Food"
          />
        )}
      </div>
    </>
  );
};

export default EatFood;
