import React, { useState } from 'react';
import './PickBozo.css';
import { GAME_WIDTH } from '../constants';
import bozo from '../assets/imgs/bozo.png';
import bozoSilhouette from '../assets/imgs/bozo_silhouette.png';

interface Character {
  id: number;
  name: string;
  image: string;
  description: string;
}

const characters: Character[] = [
  {
    id: 1,
    name: "L Bozo",
    image: bozo,
    description: "Your basic bozo"
  },
  {
    id: 2,
    name: "Winner Bozo",
    image: bozoSilhouette,
    description: "You won something for once."
  },
  {
    id: 3,
    name: "Bozo",
    image: bozoSilhouette,
    description: "Bozo 3"
  },
  {
    id: 4,
    name: "Bozo 4",
    image: bozoSilhouette,
    description: "Bozo 4"
  },
  {
    id: 5,
    name: "Bozo 5",
    image: bozoSilhouette,
    description: "Bozo 5"
  },
];

interface PickBozoProps {
  handlePickBozo: () => void;
}

const PickBozo: React.FC<PickBozoProps> = ({ handlePickBozo }) => {
  const NUM_CHARACTERS = 5;
  const [selectedCharacter, setSelectedCharacter] = useState<Character | null>(null);

  const handleCharacterClick = (character: Character) => {
    setSelectedCharacter(character);
  };

  return (
    <div className="pick-bozo-container">
      <h1 className="pick-bozo-title">Choose Your Character</h1>
      <p className="pick-bozo-subtitle">Select one of the characters below to continue</p>
      
      <div className="characters-grid">
        {characters.map((character) => (
          <div
            key={character.id}
            style={{ width: ((GAME_WIDTH - 700) / NUM_CHARACTERS ) }}
            className={`character-item ${selectedCharacter?.id === character.id ? 'selected' : ''}`}
            onClick={() => handleCharacterClick(character)}
          >
            <img 
              src={character.image} 
              alt={character.name} 
              className="character-image"
            />
          </div>
        ))}
      </div>

      {selectedCharacter && (
        <div className="selection-summary">
          <h2 className="selected-character-name">{selectedCharacter.name}</h2>
          <p className="selected-character-description">{selectedCharacter.description}</p>
        </div>
      )}

      <button onClick={handlePickBozo}>Begin your L Bozo Journey</button>
    </div>
  );
};

export default PickBozo;
