import React, { useState } from 'react';

import textBoxSmall from "../../../assets/imgs/text-box-small.png";
import { animate } from 'animejs';

enum MessageType {
  POSITIVE = 'POSITIVE',
  NEUTRAL = 'NEUTRAL',
  NEGATIVE = 'NEGATIVE'
}

interface StreamMessagesProps {
  moodModifierCallback: (moodModifier: number) => void;
  onDismiss?: () => void;
}

const StreamMessages: React.FC<StreamMessagesProps> = ({moodModifierCallback, onDismiss}) => {
  const [currentMessage, setCurrentMessage] = useState<string>('');

  // Positive messages
  const positivePrefixMessages = [
    "You have an incredibly productive stream. Everyone is so kind to you and you didn't even have to do any werewolf ASMR.",
    "You have one of the best streams of your life! Someone donated one thousand bozobucks to you, which translates to one free voucher for the hotdog stand outside your house, but that's awesome!",
    "You share many emotional moments with your stream and feel like your community has never been closer.",
    "You stream and have a fantastic time. You yap away for hours with your community and collab with your friends. At the end of it all you know for certain how loved you are.",
    "You stream a horse girl game for 14 hours straight, and you don't regret it. You rolled the horse you had wanted for ages and won all your races. Life doesn't get better than this."
  ];

  const positiveBozoMessages = [
    "One of the highlights was tokidokitoky and arjunbroepic spamming your chat asking when the next paint drying stream is, reminding you of all the great paint drying memories you already built together.",
    "At one point McDylanf3 posted Is there a universe in which you don't kill the bozos with laughter?, which was touching.",
    "At the end, it made you happy to see ocarinaofme post the stream was great today Bo! Boheart",
    "Even when your viewers have busy lives, seeing someone like ocarinaofme post Sorry I can't chat today! I have to lurk and work, but you are the best background noise! makes you feel good.",
    "You were supported by such generous chatters like giddybreadstick, who said You're my favourite streamer Bo, BTW when am I getting paid?",
    "A real highlight was when ChopChopBacca said this guy thrives off googoo baby time which is one of your favourite things!",
    "All the small messages make your day, like when smalllongdragon said ur a great streamer!",
    "It was amazing when you got raided and ninjablade46 spammed bozomouth <- raiders in here   bozomouth <- raiders in here   bozomouth <- raiders in here   bozomouth <- raiders in here   bozomouth <- raiders in here   bozomouth <- raiders in here"
  ];

  // Neutral messages
  const neutralPrefixMessages = [
    "You have an alright stream. There were a couple of trolls but overall the positivity of your community shone through.",
    "You have a decent time streaming. Your mods disappeared at the exact moment when someone came in and dropped an ASCII of an amogus with a penis, so you had to ban them yourself, but if anything it was a funny moment to remember.",
    "You stream and it's just an average day of streaming for your average Bo. Very average, if you had to say. Average chatting, average streaming, average spam, average donations. Average doesn't look like a real word anymore.",
    "You're okay with the stream today. It wasn't amazing but sometimes it's all about just doing what you can, and that's enough.",
    "You have one of the streams of all time. It sure was a stream. Just an alright, pretty okay stream. Wowzers."
  ];

  const neutralBozoMessages = [
    "It sure was something when tzentzen said < Insert drow copypasta here > and you had to practice self restraint.",
    "At least your community is always hungry for more, and you're not sure how to balance the demand for Expedition 33 again from ElseWhere, or how ocarinaofme wants you to play Croc again.",
    "You became the thinker for a solid 7 minutes after McDylanf3 asked you Hey Bo! Which of your friends could you beat in Are you smarter than a fifth grader?",
    "You got into a heated argument at one point after McDylanf3 asked I wonder, who's a bigger clown, Bo or Ruru?",
    "At one point you had to clarify yourself after giddybreadstick said You like the taste of breadsticks!? Monka",
    "It nearly drove you insane when Carnelian kept spamming om om om, here's some freshly grown oms for you Bo",
    "The karaoke section of your stream was fun when Carnelian posted and I'm just a teeeenageee dirtbag babyyyy~ and you broke out into song.",
    "When you had some new viewers NightingaleGemini was quick to jump in and remind them rule 1: dont look away",
    "At one point TealEmile posted stop calling me"
  ];

  // Negative messages
  const negativePrefixMessages = [
    "You have a horrible stream. How did that guy manage to doxx your address? Now you have to pack up your cardboard box and move to a different street. This sucks.",
    "You don't think streaming could get any worse than this. Frame drops every 5 seconds, and you disconnected seventeen times. May your internet service provider go up in flames.",
    "You stream and get so mad at chat that you end up back in the hospital. What a shitty day, this is everyone else's fault.",
    "You are certain that this is the worst stream to have ever existed. You got hate raided by someone called Ayer69 and they kept telling you that you're mid and will never have bitches.",
    "You stream and it just sucks. Your jokes don't land even though they were super funny? You're going to tell them to YourAverageMom later, at least she'll appreciate you."
  ];

  const negativeBozoMessages = [
    "To top it all off, viva36x said fu bo and everyone in the chat spammed only that for the next 79 minutes.",
    "It was especially hurtful when Brain_LikeCrambledEggs said no balls. You have LOTS of balls, actually.",
    "You were also actually having a good time at one point, before Elsewhere typed HeyBo can you stop rping with ChatGPT and made you sulk.",
    "You also don't know why Brain_LikeCrambledEggs felt the need to say like you would know what having friends feels like - you have lots of friends. Many friends! And those friends are definitely in the room with us right now.",
    "The worst part of all was when you desperately wanted some oms just to feel better and giddybreadstick said Sorry I couldn't om back there, was making dinner, I'll be sure to om next time my lord, I swear it. It's too late, breadstick. It's too late.",
    "For some reason, tokidokitoky also decided to bring up that I can't believe you played the fishing minigame for over 10 hours! Mind your own business. Can't a man fish in peace without judgement for once?",
    "Whilst revelling in your own failures, you were only sent deeper into the pits of despair when tutifruti73 decided to comment I would have beaten Are You Smarter than a 5th Grader? in the first round. It was a swift ban."
  ];

  const generateStreamMessage = (messageType: MessageType): string => {
    let prefixMessages: string[];
    let bozoMessages: string[];

    switch (messageType) {
      case MessageType.POSITIVE:
        prefixMessages = positivePrefixMessages;
        bozoMessages = positiveBozoMessages;
        break;
      case MessageType.NEUTRAL:
        prefixMessages = neutralPrefixMessages;
        bozoMessages = neutralBozoMessages;
        break;
      case MessageType.NEGATIVE:
        prefixMessages = negativePrefixMessages;
        bozoMessages = negativeBozoMessages;
        break;
      default:
        throw new Error('Invalid message type');
    }

    // Randomly select one prefix message and one bozo message
    const randomPrefix = prefixMessages[Math.floor(Math.random() * prefixMessages.length)];
    const randomBozo = bozoMessages[Math.floor(Math.random() * bozoMessages.length)];

    // Concatenate them with a space
    return `${randomPrefix} ${randomBozo}`;
  };

  // Generate a message when component mounts or when needed
  React.useEffect(() => {
    const messageTypes = [MessageType.POSITIVE, MessageType.NEUTRAL, MessageType.NEGATIVE];
    const randomMessageType = messageTypes[Math.floor(Math.random() * messageTypes.length)];
    const message = generateStreamMessage(randomMessageType);
    setCurrentMessage(message);
    moodModifierCallback(
      randomMessageType === MessageType.POSITIVE ? 7 :
      randomMessageType === MessageType.NEUTRAL ? 0 :
      -14
    );

    animate(".stream-messages", {
      opacity: [0, 1],
      translateY: [50, 0],
      duration: 300,
      easing: "outCirc"
    });
  }, []);

  return (
    <div className="stream-messages" style={{
      position: 'absolute',
      top: 660,
      left: 140,
      zIndex: 100000,
    }}>
      <div 
        style={{
          backgroundImage: `url(${textBoxSmall})`,
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
          width: '1080px',
          height: '280px',
          position: 'relative',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          padding: '20px'
        }}
      >
        {onDismiss && (
          <button
            onClick={onDismiss}
            style={{
              color: 'white',
              position: 'absolute',
              top: 235,
              right: 50,
              background: 'rgba(255, 0, 0, 0.8)',
              borderRadius: '50%',
              width: '30px',
              height: '30px',
              cursor: 'pointer',
              fontSize: '18px',
              fontWeight: 'bold',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              zIndex: 100001
            }}
          >
            
          </button>
        )}
        <p style={{
          fontFamily: 'KGPerfectPenmanship',
          color: 'black',
          fontSize: '27px',
          margin: "20px 0 0 0",
          lineHeight: '1.4',
          maxWidth: '90%'
        }}>
          {currentMessage}
        </p>
      </div>
    </div>
  );
};

export default StreamMessages;
