import React, { useEffect, useState, useRef } from 'react';
import { animate, createTimeline } from 'animejs';

import petHand from '../../../assets/imgs/pet-hand.png';
import petHearts from '../../../assets/imgs/pet-hearts.png';
import textBox from '../../../assets/imgs/text-box.png';

interface PetBozoProps {
  currHappiness: number;
}

const HAPPINESS_TEXT = [
  "Your bozo has dark rings under their eyes and a hollow gauntness that reeks of death. You caught them googling what happens at the glue factory on their phone earlier.",
  "Your bozo seems pretty down. Youre not sure when you last saw them smiling, but they still have some energy at least. Maybe they just need a bit more love.",
  "Your bozo is doing alright. Theyre trapped in the eternal battle of life where they take each day as it comes, sometimes good and sometimes bad. But that rat tail is still wagging and youre right there with them.",
  "Your bozo seems very happy! You guys binged Urusei Yatsura recently and are planning a Zelda-thon soon for the stream. Youve bonded over your interests and are really getting close. It feels good to have a friend like this bozo.",
  "Your bozo is brimming with joy! Every day they have a sparkle in their eyes and a skip in their step. As a way of thanking you for your friendship, they sent you a detailed fanfiction script of Bo x Elven Warrior Woman which you put away to read later. This bozo understands you perfectly, and you them. Youre best friends.",
]

const PetBozo: React.FC<PetBozoProps> = ({ currHappiness }) => {
  const petHandRef = useRef<HTMLDivElement>(null);
  const petHeartsRef = useRef<HTMLDivElement>(null);
  const [currHappinessText, setCurrHappinessText] = useState(HAPPINESS_TEXT[0]);
  const [isTextBoxVisible, setIsTextBoxVisible] = useState(false);

  const determineHappinessText = (happiness: number) => {
    // Determine which text to show based on happiness intervals of 20
    // 0-20: index 0, 21-40: index 1, 41-60: index 2, 61-80: index 3, 81-100: index 4
    const index = Math.min(Math.floor(happiness / 20), HAPPINESS_TEXT.length - 1);
    return HAPPINESS_TEXT[index];
  }

  const closeTextBox = () => {
    animate(".text-box-container", {
      opacity: [1, 0],
      translateY: [0, 50],
      duration: 300,
      easing: "outCirc",
      onComplete: () => {
        setIsTextBoxVisible(false);
      }
    });
  };

  // Update happiness text when currHappiness changes
  useEffect(() => {
    const newText = determineHappinessText(currHappiness);
    setCurrHappinessText(newText);
    
    animate(".text-box-container", {
      opacity: 0,
      translateY: 50,
      duration: 0,
    });
    
    // Show the text box
    setIsTextBoxVisible(true);
    
    setTimeout(() => {
      animate(".text-box-container", {
        opacity: [0, 1],
        translateY: [50, 0],
        duration: 300,
        easing: "outCirc"
      });
    }, 10);
  }, []);

  useEffect(() => {
    // Force reset the DOM elements to their initial state
    if (petHandRef.current) {
      petHandRef.current.style.transform = 'none';
      petHandRef.current.style.opacity = '0';
    }
    if (petHeartsRef.current) {
      petHeartsRef.current.style.transform = 'none';
      petHeartsRef.current.style.opacity = '0';
    }

    // Stop any existing animations on these elements and reset transforms
    animate(".pet-hand-container", {
      translateY: 0,
      translateX: 0,
      rotate: 0,
      scale: 1,
      opacity: 0,
      duration: 0
    });

    animate(".pet-hearts-container", {
      translateY: 0,
      translateX: 0,
      rotate: 0,
      scale: 1,
      opacity: 0,
      duration: 0
    });

    // Small delay to ensure reset is complete
    setTimeout(() => {
      // Create and store the main timeline
      const mainTimeline = createTimeline();

      // Add pet hand animation
      mainTimeline.add(".pet-hand-container", {
        translateY: [-30, 0],
        opacity: [0, 1],
        duration: 500,
      }, 100)
        .add(".pet-hand-container", {
          translateY: [0, -30, 0, -30, 0],
          duration: 500,
        })
        .add(".pet-hand-container", {
          translateY: [0, -30],
          opacity: [1, 0],
          duration: 500,
          delay: 800,
        });

      // Add pet hearts animation
      mainTimeline.add(".pet-hearts-container", {
        opacity: [0, 1, 0],
        translateY: [0, -60, -90],
        duration: 1400,
        ease: 'inOut',
        delay: 1000,
      }, 0);
    }, 50);

    // Clean up function
    return () => {
      // Force reset the DOM elements
      if (petHandRef.current) {
        petHandRef.current.style.transform = 'none';
        petHandRef.current.style.opacity = '0';
      }
      if (petHeartsRef.current) {
        petHeartsRef.current.style.transform = 'none';
        petHeartsRef.current.style.opacity = '0';
      }

      // Stop any ongoing animations when component unmounts
      animate(".pet-hand-container", {
        translateY: 0,
        translateX: 0,
        rotate: 0,
        scale: 1,
        opacity: 0,
        duration: 0
      });

      animate(".pet-hearts-container", {
        translateY: 0,
        translateX: 0,
        rotate: 0,
        scale: 1,
        opacity: 0,
        duration: 0
      });
    };
  }, []);

  return (
    <>
      <div ref={petHandRef} className="pet-hand-container" style={{ position: 'absolute', left: 40, top: 20, opacity: 0 }}>
        <img src={petHand} alt="Pet Hand" />
      </div>
      <div ref={petHeartsRef} className="pet-hearts-container" style={{ position: 'absolute', left: -80, top: 120, opacity: 0 }}>
        <img src={petHearts} alt="Pet Hearts" />
      </div>

      {isTextBoxVisible && (
        <div className="text-box-container" style={{ position: 'absolute', left: -375, top: 400, width: 100, height: 100, zIndex: 100000, opacity: 0 }}>
          <img src={textBox} alt="Text Box" />
          <div style={{
            position: 'absolute',
            left: 70,
            top: 90,
            width: 1000,
            fontSize: 33,
            color: 'black',
            fontFamily: 'KGPerfectPenmanship',
          }}>
            {currHappinessText}
          </div>
          <button
            onClick={closeTextBox}
            style={{
              position: 'absolute',
              right: -950,
              top: 315,
              background: 'red',
              color: 'white',
              border: 'none',
              borderRadius: '50%',
              width: '30px',
              height: '30px',
              fontSize: '16px',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontWeight: 'bold',
            }}
            title="Close"
          >
            
          </button>
        </div>
      )}
    </>
  );
};

export default PetBozo;
