import React, { useState, useEffect } from 'react';
import { GAME_WIDTH, GAME_HEIGHT, STARTING_MONEY, FOOD_COST, MARGINS } from '../../constants';
import './Game.css';

// Import image assets
import bozoRoomBg from '../../assets/imgs/bozo-room-bg.png';
import buttonClean from '../../assets/imgs/button-clean.png';
import buttonFeed from '../../assets/imgs/button-feed.png';
import buttonNextDay from '../../assets/imgs/button-next-day.png';
import buttonPet from '../../assets/imgs/button-pet.png';
import buttonPlay from '../../assets/imgs/button-play.png';
import buttonStream from '../../assets/imgs/button-stream.png';
import MoneyDisplay from "../../assets/imgs/money-counter.png";
import DayDisplay from "../../assets/imgs/day-counter.png";
import NightDisplay from "../../assets/imgs/day-counter-night.png";
import bozo from '../../assets/imgs/bozo.gif';
import bozoStatic from '../../assets/imgs/bozo-bozo.png';
import failScreen from '../../assets/imgs/state-failure.png';
import bigVictoryScreen from '../../assets/imgs/state-victory.png';
import trash1 from '../../assets/imgs/trash-mcdonalds.png';
import trash2 from '../../assets/imgs/trash-pizza.png';
import trash3 from '../../assets/imgs/trash-split-fiction.png';
import dayCounterOne from '../../assets/imgs/day-counter-one.png';
import dayCounterTwo from '../../assets/imgs/day-counter-two.png';
import dayCounterThree from '../../assets/imgs/day-counter-three.png';
import dayCounterFour from '../../assets/imgs/day-counter-four.png';
import dayCounterFive from '../../assets/imgs/day-counter-five.png';
import dayCounterSix from '../../assets/imgs/day-counter-six.png';
import dayCounterSeven from '../../assets/imgs/day-counter-seven.png';
import dayCounterEight from '../../assets/imgs/day-counter-eight.png';
import dayCounterNine from '../../assets/imgs/day-counter-nine.png';
import dayCounterTen from '../../assets/imgs/day-counter-ten.png';
import dayCounterEleven from '../../assets/imgs/day-counter-eleven.png';
import dayCounterTwelve from '../../assets/imgs/day-counter-twelve.png';
import stateVictoryText from '../../assets/imgs/state-victory-text.png';
import firstRaceWin from '../../assets/imgs/trophy-bronze.png';
import secondRaceWin from '../../assets/imgs/trophy-silver.png';
import thirdRaceWin from '../../assets/imgs/trophy-gold.png';

import EatFood from './EatFood/EatFood';
import PetBozo from './PetBozo/PetBozo';
import StreamPc from './Stream/StreamPc';
import Race from './Race/Race';
import { animate } from 'animejs';
import DND from './DND/DND';
import StreamMessages from './Stream/StreamMessages';
interface GameProps {
  onReturnToMainMenu?: () => void;
}

export const RACER_NAME = "Bozo";

const RACE_DAYS = new Set([4, 8, 12]);
const GARBAGE_SPRITES = [trash1, trash2, trash3];

const Game: React.FC<GameProps> = ({ onReturnToMainMenu }) => {
  const [money, setMoney] = useState(STARTING_MONEY);
  const [currDay, setCurrDay] = useState(1);
  const [happiness, setHappiness] = useState(50);
  const [isRoomDirty, setIsRoomDirty] = useState(false);
  const [selectedDirtySprite, setSelectedDirtySprite] = useState(0);
  const [hasPlayed, setHasPlayed] = useState(false);
  const [hasFinishedPlayingDnd, setHasFinishedPlayingDnd] = useState(false);
  const [hasPetBozo, setHasPetBozo] = useState(false);
  const [hasFedBozo, setHasFedBozo] = useState(false);
  const [isMainUiVisible, setIsMainUiVisible] = useState(true);
  const [hasStreamed, setHasStreamed] = useState(false);
  const [isStreamMessagesDismissed, setIsStreamMessagesDismissed] = useState(false);
  const [isInMainRoom, setIsInMainRoom] = useState(true);
  const [raceScreen, setRaceScreen] = useState<'race' | 'victory' | 'failure' | 'gameover'>('race');
  const [raceNumber, setRaceNumber] = useState(0);
  const [isFading, setIsFading] = useState(false);
  const [fadeComplete, setFadeComplete] = useState(false);
  const [hasDoneBigAction, setHasDoneBigAction] = useState(false);
  const [isNextDayButtonHovered, setIsNextDayButtonHovered] = useState(false);
  const [isNextDayButtonClicked, setIsNextDayButtonClicked] = useState(false);
  const [hasWonFirstRace, setHasWonFirstRace] = useState(false);
  const [hasWonSecondRace, setHasWonSecondRace] = useState(false);
  const [hasWonThirdRace, setHasWonThirdRace] = useState(false);

  // Quarter circle parameters
  const radius = 200;
  const strokeWidth = 3;
  const textRadius = radius + 30; // Text follows a slightly larger radius

  const buttonWidth = (GAME_WIDTH - 200) / 5;

  // State for confirmation popup
  const [showFeedConfirmation, setShowFeedConfirmation] = useState(false);
  const [isExiting, setIsExiting] = useState(false);

  // Handle confirmation actions
  const handleConfirmFeed = () => {
    if (money >= FOOD_COST) {
      setMoney(money - FOOD_COST);
      setHasFedBozo(true);
      console.log('Feeding confirmed! Money deducted:', FOOD_COST);
    } else {
      console.log('Not enough money to feed!');
    }
    handleExitPopup();
  };

  const handleCancelFeed = () => {
    handleExitPopup();
  };

  const handleExitPopup = () => {
    setIsExiting(true);
    setTimeout(() => {
      setShowFeedConfirmation(false);
      setIsExiting(false);
    }, 300); // Match the animation duration
  };

  const getNewHappiness = (currHappiness: number, modifyAmount: number) => {
    return Math.min(Math.max(currHappiness + modifyAmount, 0), 100);
  }

  // When a new day starts, randomize whether the room needs to be cleaned
  useEffect(() => {
    // If we're leaving the main room, don't do anything
    if (!isInMainRoom) {
      return;
    }

    // Check if daily tasks were completed
    if (currDay !== 1) {
      let currHappiness = happiness;
      if (isRoomDirty) {
        currHappiness = getNewHappiness(currHappiness, -10);
      } else {
        const isRoomDirty = Math.random() <= 0.80;
        setIsRoomDirty(isRoomDirty);
        const newDirtySprite = Math.floor(Math.random() * GARBAGE_SPRITES.length);
        setSelectedDirtySprite(newDirtySprite);
        // console.log(`isRoomDirty ${isRoomDirty} newDirtySprite ${newDirtySprite}`)
      }

      // General happiness decay
      // currHappiness = getNewHappiness(currHappiness, -5);

      if (!hasPetBozo) {
        currHappiness = getNewHappiness(currHappiness, -5);
      }

      if (!hasFedBozo) {
        currHappiness = getNewHappiness(currHappiness, -10);
      }
      setHappiness(currHappiness);

      // Check if happiness reached 0 or lower - game over, don't punish user if they reach 0 after a race day
      if (currHappiness <= 0 && !RACE_DAYS.has(currDay - 1)) {
        setRaceScreen('gameover');
      }
    }

    // Reset dailies
    setHasPetBozo(false);
    setHasFedBozo(false);
    setHasStreamed(false);
    setIsStreamMessagesDismissed(false);
    setHasPlayed(false);
    setHasFinishedPlayingDnd(false);
    setHasDoneBigAction(false);
    setIsStreamMessagesDismissed(false);

    // Reset brightness
    const animDuration = (currDay === 1 || !hasDoneBigAction) ? 0 : 1000;
    animate(".day-display", {
      opacity: [0, 1],
      duration: animDuration,
      easing: 'easeInOutQuad',
    })
    animate(".night-display", {
      opacity: [1, 0],
      duration: animDuration,
      easing: 'easeInOutQuad',
    })

    animate(".fade-at-night", {
      filter: ["brightness(0.5)", "brightness(1)"],
      duration: animDuration,
      easing: 'easeInOutQuad',
    })
  }, [currDay, isInMainRoom]);


  // Handle feed button click
  const handleFeedClick = () => {
    setShowFeedConfirmation(true);
    setIsExiting(false);
  };

  const handlePlayClick = () => {
    setHasPlayed(true);
  }

  const handleCleanClick = () => {
    setIsRoomDirty(false);
  }

  const handleStreamClick = () => {
    setHasStreamed(true);
    setIsStreamMessagesDismissed(false);
    setMoney(money + 25);
  }

  const handleDismissStreamMessages = () => {
    animate(".stream-messages", {
      opacity: [1, 0],
      translateY: [0, 50],
      duration: 300,
      easing: "outCirc"
    });
    animate(".stream-pc", {
      translateX: [0, 1500],
      duration: 1500,
      onComplete: () => {
        setIsStreamMessagesDismissed(true);
      }
    });
  }

  const handlePetClick = () => {
    if (!hasPetBozo) {
      setHasPetBozo(true);
      const newHappiness = getNewHappiness(happiness, 5);
      setHappiness(newHappiness);
    }
  }

  const handleRaceOutcome = (outcome: 'continue' | 'victory' | 'failure') => {
    if (outcome === 'victory') {
      setRaceScreen('victory');
    } else if (outcome === 'failure') {
      setRaceScreen('failure');
    } else {
      // Continue to next race or back to main room
      setIsInMainRoom(true);
      setRaceNumber(raceNumber + 1);
      setCurrDay(currDay + 1);
    }
  }

  const handleRaceDayClick = () => {
    setIsFading(true);
    setFadeComplete(false);
    setTimeout(() => {
      setIsInMainRoom(false);
      setRaceScreen('race');
      setTimeout(() => {
        setIsFading(false); // Hide the fade overlay after 2 seconds
      }, 2000); // Show race day intro for 2 seconds
    }, 1000); // 1 second fade
  }

  const handleNextDayButtonClick = () => {
    setIsNextDayButtonClicked(true);
    setTimeout(() => {
      setIsNextDayButtonClicked(false);
      setCurrDay(currDay + 1);
    }, 150);
  }

  const handleRaceDayButtonClick = () => {
    setIsNextDayButtonClicked(true);
    setTimeout(() => {
      setIsNextDayButtonClicked(false);
      handleRaceDayClick();
    }, 150);
  }

  useEffect(() => {
    if (hasPlayed || hasStreamed) {
      setHasDoneBigAction(true);
      setTimeout(() => {
        animate(".day-display", {
          opacity: [1, 0],
          duration: 3000,
          easing: 'easeInOutQuad',
        })
        animate(".night-display", {
          opacity: [0, 1],
          duration: 3000,
          easing: 'easeInOutQuad',
        })

        animate(".fade-at-night", {
          filter: ["brightness(1)", "brightness(0.5)"],
          duration: 3000,
          easing: 'easeInOutQuad',
        })
        animate(".bozo-room", {
          filter: ["brightness(1)", "brightness(0.5)"],
          duration: 3000,
          easing: 'easeInOutQuad',
        })
      }, 3000);
    }
  }, [hasPlayed, hasStreamed]);

  const generateDayNameDisplay = () => {
    switch (currDay) {
      case 1:
        return <img src={dayCounterOne} alt="Day 1" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 2:
        return <img src={dayCounterTwo} alt="Day 2" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 3:
        return <img src={dayCounterThree} alt="Day 3" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 4:
        return <img src={dayCounterFour} alt="Day 4" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 5:
        return <img src={dayCounterFive} alt="Day 5" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 6:
        return <img src={dayCounterSix} alt="Day 6" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 7:
        return <img src={dayCounterSeven} alt="Day 7" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 8:
        return <img src={dayCounterEight} alt="Day 8" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 9:
        return <img src={dayCounterNine} alt="Day 9" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 10:
        return <img src={dayCounterTen} alt="Day 10" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 11:
        return <img src={dayCounterEleven} alt="Day 11" style={{ position: 'absolute', top: 0, left: 0 }} />;
      case 12:
        return <img src={dayCounterTwelve} alt="Day 12" style={{ position: 'absolute', top: 0, left: 0 }} />;
      default:
        return null;
    }
  }

  const wasPlayerFirstPlace = (wasPlayerFirstPlace: boolean, raceNumber: number) => {
    if (raceNumber === 0) {
      setHasWonFirstRace(wasPlayerFirstPlace);
    } else if (raceNumber === 1) {
      setHasWonSecondRace(wasPlayerFirstPlace);
    } else if (raceNumber === 2) {
      setHasWonThirdRace(wasPlayerFirstPlace);
    }
  }

  useEffect(() => {
    console.log(`happiness ${happiness}`)
  }, [happiness]);

  return (
    <div>
      {/* Fade Transition Overlay */}
      {isFading && (
        <div style={{
          position: 'fixed',
          top: MARGINS,
          left: MARGINS,
          width: GAME_WIDTH,
          height: GAME_HEIGHT,
          backgroundColor: 'black',
          zIndex: 10000,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          animation: 'fadeIn 0.5s ease-in-out'
        }}>
          <div style={{
            color: 'white',
            fontSize: '32px',
            fontFamily: 'KGPerfectPenmanship, cursive',
            textAlign: 'center'
          }}>
            <div>Race Day!</div>
            <div style={{ fontSize: '18px', marginTop: '20px' }}>Get ready to compete...</div>
          </div>
        </div>
      )}
      {
        hasPlayed && !hasFinishedPlayingDnd &&
        <DND
          happinessUpdateCallback={(happinessModifier) => {
            setHappiness(happiness + happinessModifier);
          }}
          onReturnToHome={() => {
            setHasFinishedPlayingDnd(true);
          }}
        />
      }
      {
        !isInMainRoom &&
        <div
          className="game-container"
          style={{
            width: GAME_WIDTH,
            height: GAME_HEIGHT,
          }}
        >
          {(() => {
            switch (raceScreen) {
              case 'race':
                const animDuration = 500;
                animate(".race-day-intro", {
                  opacity: [1, 0],
                  duration: animDuration,
                  easing: 'easeInOutQuad',
                })
                setTimeout(() => {
                  setFadeComplete(true);
                }, animDuration + 10000)
                return (
                  <>
                    {
                      !fadeComplete &&
                      <div className="race-day-intro" style={{
                        position: 'fixed',
                        top: MARGINS,
                        left: MARGINS,
                        width: GAME_WIDTH,
                        height: GAME_HEIGHT,
                        backgroundColor: 'black',
                        zIndex: 10000,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}>
                        <div style={{
                          color: 'white',
                          fontSize: '32px',
                          fontFamily: 'KGPerfectPenmanship, cursive',
                          textAlign: 'center'
                        }}>
                          <div>Race Day!</div>
                          <div style={{ fontSize: '18px', marginTop: '20px' }}>Get ready to compete...</div>
                        </div>
                      </div>
                    }
                    <Race
                      raceNumber={raceNumber}
                      currRacer={{
                        name: RACER_NAME,
                        image: bozoStatic,
                        speed: happiness,
                        // speed: 100,
                        x: 0,
                        isFinished: false,
                        finishTime: null,
                        lane: 4,
                      }}
                      onRaceOutcome={handleRaceOutcome}
                      fadeComplete={fadeComplete}
                      wasPlayerFirstPlace={wasPlayerFirstPlace}
                    />
                  </>
                );
              case 'victory':
                return (
                  <div>
                    <img src={stateVictoryText} alt="Victory!" style={{
                      position: 'absolute',
                      bottom: 50,
                      left: 50,
                    }} />
                    <img
                      src={bigVictoryScreen}
                      alt="Victory!"
                    />
                    <button
                      onClick={() => {
                        setRaceScreen('race');
                        setIsInMainRoom(true);
                        setIsMainUiVisible(false);
                        setIsRoomDirty(false);
                      }}
                      style={{
                        position: 'absolute',
                        top: 20,
                        right: 20,
                        fontFamily: 'KGPerfectPenmanship',
                        fontSize: '30px',
                        backgroundColor: '#4CAF50',
                        color: 'white',
                        border: 'none',
                        borderRadius: '5px',
                        cursor: 'pointer',
                        padding: 10,
                      }}
                    >
                      Return Home
                    </button>
                  </div>
                );
              case 'gameover':
              case 'failure':
                return (
                  <div>
                    <img
                      src={failScreen}
                      alt="Game Over"
                      style={{
                        width: GAME_WIDTH,
                        height: GAME_HEIGHT,
                      }}
                    />
                    <button
                      onClick={() => {
                        if (onReturnToMainMenu) {
                          onReturnToMainMenu();
                        } else {
                          setRaceScreen('race');
                          setIsInMainRoom(true);
                        }
                      }}
                      style={{
                        position: 'absolute',
                        top: 900,
                        left: 550,
                        fontSize: '24px',
                        fontFamily: 'KGPerfectPenmanship',
                        padding: '12px 20px',
                        backgroundColor: '#f44336',
                        color: 'white',
                        border: 'none',
                        borderRadius: '8px',
                        cursor: 'pointer',
                        fontWeight: 'bold',
                        boxShadow: '0 4px 8px rgba(0,0,0,0.2)'
                      }}
                    >
                      Return to Main Menu
                    </button>
                  </div>
                );
              default:
                return null;
            }
          })()}

          {/* Race Day Intro Overlay - appears on top of race screen */}
          {isFading && !isInMainRoom && (
            <div style={{
              position: 'fixed',
              top: MARGINS,
              left: MARGINS,
              width: GAME_WIDTH,
              height: GAME_HEIGHT,
              backgroundColor: 'black',
              zIndex: 10000,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              animation: 'fadeOut 2s ease-in-out'
            }}>
              <div style={{
                color: 'white',
                fontSize: '32px',
                fontFamily: 'KGPerfectPenmanship, cursive',
                textAlign: 'center'
              }}>
                <div>Race Day!</div>
                <div style={{ fontSize: '18px', marginTop: '20px' }}>Get ready to compete...</div>
              </div>
            </div>
          )}
        </div>
      }
      {
        isInMainRoom &&
        <div
          className="game-container"
          style={{
            width: GAME_WIDTH,
            height: GAME_HEIGHT,
          }}
        >
          <img className="bozo-room fade-at-night" src={bozoRoomBg} style={{
            position: 'absolute',
            top: 0,
            left: 0,
            backgroundImage: `url(${bozoRoomBg})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat'
          }} alt="Bozo Room"/>
          {/* Game Over Screen - shown when happiness reaches 0 */}
          {raceScreen === 'gameover' && (
            <div>
              <img
                src={failScreen}
                alt="Game Over"
                style={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  zIndex: 1000,
                  width: GAME_WIDTH,
                  height: GAME_HEIGHT,
                }}
              />
              <button
                onClick={() => {
                  if (onReturnToMainMenu) {
                    onReturnToMainMenu();
                  } else {
                    setHappiness(50); // Reset happiness
                    setIsInMainRoom(true);
                  }
                }}
                style={{
                  position: 'absolute',
                  top: 900,
                  left: 550,
                  fontSize: '24px',
                  fontFamily: 'KGPerfectPenmanship',
                  padding: '12px 20px',
                  backgroundColor: '#f44336',
                  color: 'white',
                  border: 'none',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  fontWeight: 'bold',
                  boxShadow: '0 4px 8px rgba(0,0,0,0.2)',
                  zIndex: 1000,
                }}
              >
                Return to Main Menu
              </button>
            </div>
          )}
          {/* Main UI - Conditionally rendered */}
          {isMainUiVisible && (
            <>
              <img
                src={MoneyDisplay}
                alt="Money Display"
                style={{
                  position: 'absolute',
                  top: 126,
                }}
              />
              <img
                className="night-display"
                src={ NightDisplay }
                alt="Night Display"
                style={{
                  position: 'absolute',
                  top: 0,
                }}
              />
              <img
                className="day-display"
                src={ DayDisplay }
                alt="Day Display"
                style={{
                  position: 'absolute',
                  top: 0,
                }}
              />
              {generateDayNameDisplay()}
              <div style={{ fontFamily: 'KGPerfectPenmanship', fontSize: 24, color: 'black', position: 'absolute', top: 140, left: 180, fontWeight: 'bold' }}>${money}</div>

              {/* Top buttons row */}
              <div className="top-buttons-container">
                <button
                  disabled={hasFedBozo}
                  className="image-button"
                  style={{
                    backgroundImage: `url(${buttonFeed})`,
                    opacity: hasFedBozo ? 0.35 : 1,
                  }}
                  onClick={handleFeedClick}
                />
                <button
                  disabled={hasDoneBigAction}
                  className="image-button"
                  style={{
                    backgroundImage: `url(${buttonPlay})`,
                    opacity: hasDoneBigAction ? 0.35 : 1,
                  }}
                  onClick={handlePlayClick}
                />
                <button
                  disabled={!isRoomDirty}
                  className="image-button"
                  style={{
                    backgroundImage: `url(${buttonClean})`,
                    opacity: !isRoomDirty ? 0.35 : 1,
                  }}
                  onClick={handleCleanClick}
                />
                <button
                  disabled={ hasDoneBigAction }
                  className="image-button"
                  style={{
                    backgroundImage: `url(${buttonStream})`,
                    opacity: hasDoneBigAction ? 0.35 : 1,
                  }}
                  onClick={handleStreamClick}
                />
                <button
                  disabled={hasPetBozo}
                  className="image-button"
                  style={{
                    backgroundImage: `url(${buttonPet})`,
                    opacity: hasPetBozo ? 0.35 : 1,
                  }}
                  onClick={handlePetClick}
                />
              </div>
            </>
          )}

          {/* Confirmation Popup */}
          {showFeedConfirmation && (
            <div style={{ fontFamily: 'KGPerfectPenmanship' }} className={`popup-overlay ${isExiting ? 'fade-out' : ''}`}>
              <div className={`popup-content ${isExiting ? 'slide-out' : ''}`}>
                <h3>Purchase Food?</h3>
                <p>Feeding your Bozo will cost ${FOOD_COST}</p>
                <div className="popup-buttons">
                  <button style={{ fontFamily: 'KGPerfectPenmanship' }} className="popup-button cancel" onClick={handleCancelFeed}>
                    Let my son starve
                  </button>
                  <button
                    className="popup-button confirm"
                    disabled={money < FOOD_COST || hasFedBozo}
                    style={{
                      fontFamily: 'KGPerfectPenmanship',
                      opacity: (money < FOOD_COST || hasFedBozo) ? 0.5 : 1,
                      cursor: (money < FOOD_COST || hasFedBozo) ? 'not-allowed' : 'pointer'
                    }}
                    onClick={handleConfirmFeed}
                  >
                    FEED BOZO NOW!!!
                  </button>
                </div>
              </div>
            </div>
          )}
          {
            isRoomDirty && selectedDirtySprite === 0 &&
            <img className="fade-at-night" src={trash1} alt="Happy Meal & Labubu" style={{
              position: 'absolute',
              top: 550,
              left: 1050,
            }} />
          }
          {
            isRoomDirty && selectedDirtySprite === 1 &&
            <img className="fade-at-night" src={trash2} alt="Domino's Pizza" style={{
              position: 'absolute',
              top: 700,
              left: 200,
            }} />
          }
          {
            isRoomDirty && selectedDirtySprite === 2 &&
            <img className="fade-at-night" src={trash3} alt="Split Fiction" style={{
              position: 'absolute',
              top: 850,
              left: 800,
            }} />
          }

          {
            hasWonFirstRace &&
            <img className="fade-at-night" src={firstRaceWin} alt="First Race Win" style={{
              position: 'absolute',
              top: 450,
              left: 80,
            }} />
          }
          {
            hasWonSecondRace &&
            <img className="fade-at-night" src={secondRaceWin} alt="Second Race Win" style={{
              position: 'absolute',
              top: 247,
              left: 280,
            }} />
          }
          {
            hasWonThirdRace &&
            <img className="fade-at-night" src={thirdRaceWin} alt="Third Race Win" style={{
              position: 'absolute',
              top: 260,
              left: 400,
            }} />
          }

          {
            hasStreamed && !isStreamMessagesDismissed &&
            <>
              <StreamPc />
              <StreamMessages 
                moodModifierCallback={(moodModifier) => {
                  setHappiness(happiness + moodModifier);
                }}
                onDismiss={handleDismissStreamMessages}
              />
            </>
          }
          <div
            style={{
              position: 'absolute',
              top: 194,
              left: 546,
            }}
          >
            <img
              className="fade-at-night"
              src={bozo}
              style={{ position: 'absolute', top: 0, left: 0 }}
              alt="Bozo"
            />

            {
              hasFedBozo &&
              <EatFood />
            }
            {
              hasPetBozo &&
              <PetBozo currHappiness={happiness} />
            }
          </div>

          {/* Next Day Button */}
          {isMainUiVisible && !RACE_DAYS.has(currDay) && (
            <img
              src={buttonNextDay}
              alt="Next Day"
              style={{
                position: 'absolute',
                top: isNextDayButtonHovered ? 845 : 850,
                right: 50,
                cursor: 'pointer',
                filter: isNextDayButtonClicked 
                  ? 'brightness(0.7) drop-shadow(0 4px 8px rgba(0,0,0,0.3))' 
                  : isNextDayButtonHovered 
                    ? 'brightness(1.2) drop-shadow(0 2px 4px rgba(0,0,0,0.2))' 
                    : 'brightness(1)',
                transition: 'all 0.15s ease-in-out',
                transform: isNextDayButtonClicked ? 'translateY(2px)' : 'translateY(0)',
              }}
              onMouseEnter={() => setIsNextDayButtonHovered(true)}
              onMouseLeave={() => setIsNextDayButtonHovered(false)}
              onClick={handleNextDayButtonClick}
            />
          )}
          {/* Race Day Button */}
          {isMainUiVisible && RACE_DAYS.has(currDay) && !isFading && (
            <button className="next-day-button" onClick={handleRaceDayClick}>
              Race Day
            </button>
          )}
        </div>
      }
    </div>
  );
};

export default Game;
